<template>
  <div>
    <v-tabs>
      <v-tab></v-tab>
<!--      <v-tab></v-tab>-->
      <v-tab-item>
        <v-card>
<!--          <v-card-title>-->
<!--            <span class="headline"></span>-->
<!--          </v-card-title>-->
          <v-card-text>
            <v-form>
              <v-card flat>
                <v-card-title></v-card-title>
                <v-card-text>
                  <v-row>
                    <v-col
                        cols="12"
                        md="2"
                    >
                      <v-text-field
                          label=""
                          v-model="skill.base.name"
                          outlined
                          dense
                      >
                      </v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        md="1"
                    >
                      <v-select
                          :items="[2,4,6]"
                          label=""
                          v-model="skill.base.round"
                          outlined
                          dense
                      ></v-select>
                    </v-col>
                    <v-col
                        cols="12"
                        md="6"
                    />
                    <v-col
                        cols="12"
                        md="2"
                    >
                      <v-text-field
                          readonly
                          v-model="totalGrade"
                          v-on:click="clickGrade"
                          label=""
                          outlined
                          class="headline centered-input"
                      >
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row dense>
                    <v-col
                        cols="12"
                        md="1"
                    >
                      <v-select
                          :items="['','']"
                          v-on:change="elementChanged()"
                          v-model="skill.base.type"
