                          v-model="skill.base.classType"
                          label=""
                          dense
                          outlined
                      ></v-select>
                    </v-col>
                    <v-col
                        cols="12"
                        md="1"
                    >
                      <v-checkbox label="" dense outlined v-model="skill.base.isAura"
                                  v-on:change="elementChanged()"
                      />
                    </v-col>
                    <v-col
                        cols="12"
                        md="1"
                        v-if="skill.base.type == ''"
                    >
                      <v-text-field
                          v-model="skill.base.buffTurn"
                          dense
                          label=""
                          outlined
                      />
                    </v-col>

                    <v-col
                        cols="12"
                        md="1"
                        v-if="skill.base.isAura"
                    >
                      <v-text-field
                          v-on:change="elementChanged()"
                          v-model="skill.base.auraRange"
                          dense
                          label=""
