                      />
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>

              <v-card flat v-if="!(skill.base.type=='' && skill.base.isAura)">
                <v-card-title></v-card-title>
                <v-card-text>
                  <v-row v-for="row in rows" :key="row.title" dense>
                    <template v-for="col in row.cols">
                      <v-col
