                            v-model="skill.mainAttr[col.label]"
                            :suffix="col.suffix"
                            :disabled="(skill.base.isAura && !col.auraSelf) || (col.mainClass && skill.base.classType == '')"
                            :class="skill.mainAttr[col.label] != col.defValue ? 'modified-input' : ''"
                            style="width: 80%"
                            dense
                            :outlined="false"
                            v-on:change="elementChanged(col.label)"
                            />
                          <v-select
                              v-if="col.type == 'combobox'"
                              v-model="skill.mainAttr[col.label]"
                              :items="col.items"
                              :label="col.label"
                              v-on:change="elementChanged(col.label)"
                              dense
                              chips
                              multiple/>
                      </v-col>
                    </template>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-card flat v-if="skill.base.isAura">
                <v-card-title></v-card-title>
                <v-card-text>
                  <v-row v-for="row in rows" :key="row.title" dense>
                    <template v-for="col in row.cols">
                      <v-col
                          cols="12"
                          :sm="col.wide || 1"
                          :key="col.label"
                          align-self="end"
                      >
                        <v-text-field
                            v-if="col.type != 'combobox'"
                            :label="col.label"
                            v-model="skill.auraAttr[col.label]"
                            :suffix="col.suffix"
                            :class="skill.auraAttr[col.label] != col.defValue ? 'modified-input' : ''"
                            :disabled="col.mainClass && skill.base.classType == ''"
                            style="width: 80%"
                            v-on:change="elementChanged(col.label)"
                            dense/>
                        <v-combobox
                            v-if="col.type == 'combobox'"
                            v-model="skill.auraAttr[col.label]"
                            v-on:change="elementChanged(col.label)"
                            :items="col.items"
                            :label="col.label"
                            dense
                            chips
                            multiple/>
                      </v-col>
                    </template>
                  </v-row>
                </v-card-text>
              </v-card>
              <v-row dense>
                <!--                  v-model="sheetContext"-->
