              filled
              outlined
              hide-details
          ></v-textarea>
        </div>
      </v-sheet>
    </v-bottom-sheet>
  </div>
</template>

<script>
import {Skill, config} from "@/model/skill-sim";

const attrLayout = [
  {
    title: '',
    cols: [ '', '', '', '', '', '', '', '', '', '', '' ]
  },
  {
    title: '',
    cols: [ '', '', '', '', '', '', '']
  },
  {
    title: '',
    cols: [ '', '', '', '', '', '', '', '' ]
  },
  {
    title: '',
    cols: [ '', '', '', '', '', '', '', '', '', ''  ]
  },
  {
    title: '',
    cols: [ '', '', '' ]
  },
  {
    title: '',
    cols: [ '']
  },
]

export default {
  name: "CustomSimulatorView",
  data() {
    return {
      bottom: false,
      skill: new Skill(),
      grade: {
        desc: '',
        detail: () => '',
      },
      totalGrade: "0.00",
      gradeDetail: '',
    };
  },
  methods: {
    submit() {
      console.log(this.skill)
    },
    // getValue(col, isAura) {
    //   const attr = isAura ? this.skill.auraAttr : this.skill.mainAttr
    //   return attr.getAttr(col.label)
    // },
    elementChanged() {
      this.grade = this.skill.calcGrade()
      this.totalGrade = this.grade.totalGrade.toFixed(2)
    },
    clickGrade() {
      this.gradeDetail = this.grade.detail()
      this.bottom = true
    },
    reset() {
      this.skill = new Skill()
      this.elementChanged()
    }
  },
  computed: {
    rows() {
      const rows = attrLayout.map(row => {
        return {
          title: row.title,
          cols: row.cols
              // .filter(col => {
              //   return this.skill.base.classType != '' || !config[col].mainClass
              // })
              .map(col => {
                const colConfig = config[col]
                return {
                  ...colConfig,
                  label: col,
                  suffix: colConfig.pct ? '%' : '',
                  defValue: config.defValue(col)
                }
          })
        }
      })
      return rows
    },
  },
}
</script>

